.footer__top {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  border-radius: 15px 15px 0 0;
  background: #FAFAFA;
  align-items: stretch; }

.footer__bottom {
  font-size: 12px;
  line-height: 15px;
  padding: 15px 30px;
  margin-top: 1px;
  background: #FAFAFA; }

.footer__bottom-left, .footer__bottom-right {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.footer__bottom-left {
  margin-top: 10px;
  flex-direction: row-reverse; }

.footer__nav {
  display: flex;
  flex: 1 1 auto;
  padding: 30px 50px 30px 30px; }

.footer__menu {
  font-size: 14px;
  line-height: 25px;
  margin-right: 50px; }
  .footer__menu:last-child {
    margin-right: 0; }
  .footer__menu a {
    text-decoration: none;
    color: #000;
    font-weight: 700; }
    .footer__menu a:hover {
      color: #F78B3D; }
  .footer__menu ul {
    display: block;
    padding: 0 15px;
    margin: 0;
    flex: 1 1 auto; }
  .footer__menu li {
    display: block;
    padding: 0;
    margin: 0; }

.footer__menu-title {
  font-size: 20px;
  color: #000;
  font-weight: 200;
  margin-bottom: 20px; }

.footer__menu-body {
  display: flex;
  align-items: flex-start;
  margin: 0 -15px; }

.footer__map {
  flex: 0 0 100%;
  min-height: 100px;
  position: relative;
  overflow: hidden; }
  .footer__map iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; }

.footer__info {
  flex: 0 0 auto;
  padding: 30px;
  font-size: 14px;
  line-height: 20px; }

.footer__info-btn {
  margin-top: 30px; }

.footer__info-item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 15px; }
  .footer__info-item + .footer__info-item {
    padding-top: 15px;
    border-top: 1px solid #E0E0E0; }
  .footer__info-item p {
    margin: 0; }
  .footer__info-item a {
    color: currentColor;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.2em; }
  .footer__info-item__icon {
    flex: 0 0 auto;
    margin-right: 15px; }

.footer__privacy {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: auto; }
  .footer__privacy p {
    margin: 0; }
  .footer__privacy a {
    color: #312E76; }

.footer__privacy-body {
  flex: 1 1 auto; }

.footer__privacy-icon {
  flex: 0 0 auto;
  font-size: 25px;
  line-height: 0;
  color: #CBCBCB;
  padding-right: 15px;
  margin-right: 15px;
  border-right: 1px solid #EDEDED; }

.footer__copyright {
  flex: 1 1 auto;
  max-width: 380px;
  color: #8F8F8F;
  font-weight: 500;
  margin-right: 30px; }

.footer__location {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1 auto;
  color: #202020;
  margin-right: 30px; }
  .footer__location p {
    margin: 0; }
  .footer__location__title {
    font-weight: 500; }
  .footer__location__icon {
    color: #CBCBCB;
    font-size: 25px;
    margin-right: 10px; }

.footer__payment {
  flex: 0 0 auto;
  font-size: 0;
  line-height: 0;
  margin-left: auto; }
  .footer__payment ul {
    display: flex;
    align-items: center;
    padding: 0;
    margin: 0 10px; }
    .footer__payment ul li {
      display: inline-block;
      padding: 0 10px;
      margin: 0; }

@media (min-width: 1280px) {
  .footer__nav {
    max-width: 490px; }
  .footer__map {
    flex: 1 1 auto; }
  .footer__info {
    order: 3; } }

@media (min-width: 1440px) {
  .footer__bottom {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: row-reverse; }
  .footer__bottom-right {
    flex: 1 1 auto; }
  .footer__bottom-left {
    flex: 0 1 auto;
    flex-direction: row; }
  .footer__privacy {
    flex: 0 0 auto;
    margin: 0 30px 0 0; } }
