.banner {
  text-align: center;
  color: #fff;
  padding: 30px;
  border-radius: 6px;
  background: #F78B3D linear-gradient(to bottom, rgba(255, 255, 255, 0.2), #F78B3D);
  font-size: 16px;
  font-weight: 500; }
  .banner__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px; }
  .banner__button {
    margin-top: 20px; }
  .banner.banner--small {
    padding: 20px;
    font-size: 14px; }
    .banner.banner--small .banner__title {
      font-size: 15px;
      margin-bottom: 0.5em; }
    .banner.banner--small .banner__button {
      margin-top: 15px; }
