@keyframes color-preview {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes color-preview-body {
  0% {
    transform: translateY(20px); }
  100% {
    transform: translateY(0); } }

.color-preview {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  animation: color-preview 0.3s ease-out;
  z-index: 300; }
  .color-preview__head {
    margin-bottom: 15px; }
  .color-preview__main {
    padding: 15px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    animation: color-preview-body 0.3s ease-out; }
  .color-preview__title {
    font-size: 14px;
    font-weight: 500; }
  .color-preview__image {
    font-size: 0;
    line-height: 0;
    display: block;
    position: relative;
    height: 160px;
    width: 160px;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid #fff;
    margin: -95px auto 15px auto; }
    .color-preview__image img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .color-preview__close {
    position: absolute;
    right: 15px;
    top: 15px;
    color: #4A4A4A;
    font-size: 20px;
    line-height: 0;
    cursor: pointer; }
  .color-preview__button {
    margin-top: 15px; }
  .color-preview.is-active {
    display: block; }
  @media (min-width: 1024px) {
    .color-preview {
      position: static;
      background: none; }
      .color-preview__close {
        display: none; }
      .color-preview__head {
        margin-bottom: 20px; }
      .color-preview__main {
        position: static;
        padding: 0;
        background: none;
        animation: none; }
      .color-preview__image {
        margin-top: 0;
        border: none;
        width: 130px;
        height: 130px; }
      .color-preview__title {
        font-weight: 400;
        font-size: 1em; } }
