.section-title {
  font-size: 18px;
  color: #000;
  font-weight: 500;
  margin: 0; }
  .section-title__icon {
    flex: 0 0 auto;
    font-size: 25px;
    line-height: 0;
    width: 1em;
    height: 1em;
    margin-right: 0.3em;
    position: relative;
    color: #F78B3D; }
  .section-title__title {
    font-size: 1em;
    color: currentColor;
    flex: 1 1 auto;
    margin: 0;
    font-weight: 500; }
  .section-title.has-icon {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .section-title.section-title--thin {
    font-weight: 200; }
  @media (min-width: 1024px) {
    .section-title {
      font-size: 32px; }
      .section-title .section-title__icon {
        font-size: 30px; }
      .section-title.section-title--small {
        font-size: 25px; } }
