.slider-range {
  display: block;
  position: relative;
  font-size: 0;
  color: #F78B3D;
  background: #F8F8F8;
  border-radius: 10px;
  height: 6px;
  padding: 0 8px;
  margin: 5px 0; }
  .slider-range .ui-slider .ui-slider-range {
    background: currentColor;
    border-radius: inherit; }
  .slider-range .ui-slider .ui-slider-handle {
    display: block;
    top: -5px;
    margin-left: -8px;
    cursor: pointer;
    outline: none;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #fff;
    border: 5px solid currentColor; }
  .slider-range .ui-slider-horizontal {
    border-radius: inherit;
    height: 100%;
    background: inherit; }
