.form-range__inputs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 15px; }

.form-range__from, .form-range__to {
  flex: 1 1 auto;
  position: relative; }
  .form-range__from input, .form-range__to input {
    padding-left: 30px;
    font-weight: 700; }

.form-range__label {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  position: absolute;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  left: 0;
  top: 0;
  width: 25px;
  height: 100%;
  color: #7A7A7A;
  font-size: 12px;
  font-weight: 400; }
  .form-range__label[for] {
    cursor: pointer; }

.form-range__sep {
  font-size: 0;
  width: 8px;
  height: 1px;
  margin: 0 6px;
  flex: 0 0 auto;
  background: #C4C4C4; }
