.owl-carousel {
  direction: ltr; }
  .owl-carousel .owl-item img {
    width: auto; }
  .owl-carousel .owl-nav button.owl-prev,
  .owl-carousel .owl-nav button.owl-next {
    display: block;
    cursor: pointer;
    border: none;
    padding: 0;
    position: absolute;
    font-size: 15px;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    color: #fff;
    background: #F78B3D;
    top: 50%;
    margin-top: -1em; }
    .owl-carousel .owl-nav button.owl-prev:hover,
    .owl-carousel .owl-nav button.owl-next:hover {
      background-color: #f56e0c; }
    .owl-carousel .owl-nav button.owl-prev.disabled,
    .owl-carousel .owl-nav button.owl-next.disabled {
      visibility: hidden; }
  .owl-carousel .owl-nav button.owl-prev {
    left: 0; }
  .owl-carousel .owl-nav button.owl-next {
    right: 0; }
  .owl-carousel .owl-dots {
    text-align: center;
    line-height: 0;
    font-size: 0;
    position: relative;
    direction: ltr;
    margin-top: 15px; }
    .owl-carousel .owl-dots button.owl-dot {
      display: inline-block;
      vertical-align: top;
      border-radius: 50%;
      box-shadow: none;
      padding: 0;
      outline: none;
      border: none;
      width: 6px;
      height: 6px;
      background: #E0E8EB;
      margin: 0 4px;
      transition: background 0.3s; }
      .owl-carousel .owl-dots button.owl-dot.active {
        background: #625878; }

.owl-drag {
  cursor: move; }
