.catalog__grid {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr)); }

.catalog__footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 30px; }

.catalog__loader {
  flex: 0 0 auto;
  min-width: 220px;
  margin-right: auto;
  margin-left: calc(50% - 110px); }

.catalog__filter {
  margin-bottom: 40px; }
