.option-filter {
  display: inline-block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 10px;
  line-height: 15px;
  padding: 5px 10px;
  border-radius: 20px;
  font-weight: 700;
  background: #F0F4FD;
  color: #312E76; }
  .option-filter [type="radio"],
  .option-filter [type="checkbox"] {
    display: none; }
  .option-filter__toggle, .option-filter__remove {
    position: relative;
    line-height: 0;
    font-size: 15px;
    transition: transform 0.3s ease-out; }
  .option-filter__remove {
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    color: #fff;
    background: #4A4796;
    width: 20px;
    border-radius: 0 20px 20px 0; }
  .option-filter__icon {
    font-size: 15px;
    color: #4A4796;
    vertical-align: top; }
  .option-filter.is-active {
    background: #312E76;
    color: #fff; }
    .option-filter.is-active .option-filter__icon {
      color: currentColor; }
    .option-filter.is-active .option-filter__toggle {
      transform: scale(-1); }
  .option-filter.option-filter--toggle {
    display: inline-flex;
    align-items: center; }
  .option-filter.option-filter--removable {
    padding-right: 30px; }
  @media (min-width: 1024px) {
    .option-filter {
      font-size: 13px;
      font-weight: 500; } }
