.cart-total {
  text-align: center;
  color: #fff;
  padding: 30px;
  border-radius: 6px;
  background: #F78B3D linear-gradient(to bottom, rgba(255, 255, 255, 0.2), #F78B3D); }
  .cart-total__title {
    font-size: 16px;
