.form-review__inner {
  max-width: 760px;
  margin: 0 auto; }

.form-review__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0 20px; }

.form-review__message {
  grid-column: span 3; }

.form-review__footer {
  display: flex;
  justify-content: flex-end; }

.form-review__submit {
  flex: 0 0 auto;
  min-width: 170px; }

.form-review__rating {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 20px; }

.form-review__rating-title {
  font-size: 14px;
  color: #000;
  margin-right: 5px; }
