.popup-color__inner {
  padding: 20px 15px; }

.popup-color__head {
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #F5F5F5; }

@media (min-width: 1024px) {
  .popup-color__inner {
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-direction: row-reverse;
    padding: 0; }
  .popup-color__main, .popup-color__aside {
    padding: 20px 25px; }
  .popup-color__main {
    flex: 1 1 auto; }
  .popup-color__aside {
    flex: 0 0 285px;
    max-width: 285px;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.25); }
  .popup-color__head {
    padding-bottom: 0;
    margin-bottom: 20px;
    border-bottom: none; }
  .popup-color__preview {
    padding-top: 20px;
    margin-top: 20px;
    border-top: 1px solid #F5F5F5; } }
