.button-rating {
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-start; }
  .button-rating [type="radio"],
  .button-rating [type="checkbox"] {
    display: none; }
  .button-rating__item {
    display: block;
    color: #EAEAEA;
    font-size: 20px;
    line-height: 0;
    cursor: pointer; }
    :checked ~ .button-rating__item {
      color: #FFC700; }
  @media (min-width: 1024px) {
    .button-rating__item {
      font-size: 25px; } }
