.form-product {
  --divider: #F1F1F1; }
  .form-product__main {
    padding-bottom: 20px;
    margin-bottom: 15px;
    border-bottom: 1px solid var(--divider); }
  .form-product__rating {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid var(--divider); }
  .form-product__rating-title {
    color: #312E76;
    margin: 0 5px; }
  .form-product__pricing {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    line-height: 1;
    margin-bottom: 15px; }
  .form-product__price {
    font-size: 22px;
    font-weight: 700; }
  .form-product__price-old {
    font-size: 13px;
    color: #434343;
    text-decoration: line-through;
    margin: 0 5px; }
  .form-product__submit, .form-product__credit {
    margin-top: 15px; }
  .form-product__specs dl {
    display: block;
    font-weight: 600;
    padding-top: 15px;
    margin-top: 15px;
    border-top: 1px solid var(--divider); }
  .form-product__specs dt,
  .form-product__specs dd {
    display: inline; }
  .form-product__info {
    margin-bottom: 20px; }
  .form-product__info-item {
    margin-bottom: 15px; }
  .form-product__info-title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 10px; }
  .form-product__size {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  .form-product__size-item {
    font-weight: 700; }
    .form-product__size-item span {
      display: block; }
  .form-product__size-sep {
    display: block;
    flex: 1 0 10px;
    position: relative;
    font-size: 0;
    line-height: 0;
    height: 10px;
    margin: 0 10px; }
    .form-product__size-sep::before, .form-product__size-sep::after {
      display: block;
      position: absolute;
      content: '';
      left: 50%;
      top: 50%;
      margin-left: -0.5px;
      margin-top: -5px;
      width: 1px;
      height: 10px;
      background: #BDBDBD; }
    .form-product__size-sep::before {
      transform: rotate(45deg); }
    .form-product__size-sep::after {
      transform: rotate(-45deg); }
  .form-product__colors {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 15px; }
  .form-product__color {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    font-weight: 400; }
    .form-product__color__image {
      font-size: 0;
      line-height: 0;
      border-radius: 50%;
      overflow: hidden;
      position: relative;
      flex: 0 0 30px;
      width: 30px;
      height: 30px;
      margin-right: 10px; }
      .form-product__color__image img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        object-fit: cover; }
    .form-product__color__main {
      flex: 1 1 auto; }
    .form-product__color__name {
      font-weight: 700; }
  .form-product__banner {
    margin: 20px 0; }
  .form-product.form-product--grey {
    --divider: #E4E4E4; }
  @media (min-width: 1024px) {
    .form-product__price {
      font-size: 26px; }
    .form-product__price-old {
      color: #000; } }
