@keyframes popup-show {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes popup-inner {
  from {
    opacity: 0;
    transform: translateY(20px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

.popup {
  display: none;
  position: fixed;
  overflow: hidden;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background: rgba(0, 0, 0, 0.5);
  animation: popup-show 0.3s ease-out; }
  .popup.is-active {
    display: block; }
  .popup__inner {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-height: 100%;
    animation: popup-inner 0.3s ease-out; }
  .popup__head {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex: 0 0 auto;
    color: #fff;
    background: #F78B3D;
    font-size: 15px;
    line-height: 20px;
    font-weight: 600;
    padding: 15px; }
  .popup__body {
    background: #fff;
    flex: 1 1 auto;
    overflow: auto;
    overflow-scrolling: touch;
    -webkit-overflow-scrolling: touch; }
  .popup__icon, .popup__close {
    font-size: 20px;
    line-height: 0;
    flex: 0 0 auto; }
  .popup__icon {
    margin-right: 10px; }
  .popup__close {
    cursor: pointer;
    margin-left: 10px; }
  .popup__title {
    flex: 1 1 auto; }
  .popup.popup--black {
    color: #fff; }
    .popup.popup--black .popup__head {
      background: #2C2C2C; }
    .popup.popup--black .popup__body {
      background: #2C2C2C; }
  @media (min-width: 1024px) {
    .popup {
      padding: 20px;
      align-items: center;
      justify-content: center;
      overflow: auto;
      overflow-scrolling: touch;
      -webkit-overflow-scrolling: touch; }
      .popup__head {
        padding-left: 25px;
        padding-right: 20px; }
      .popup__inner {
        display: block;
        margin: auto;
        position: relative;
        max-height: none;
        width: 360px;
        border-radius: 10px;
        overflow: hidden; }
      .popup.is-active {
        display: flex; }
      .popup.popup--large .popup__inner {
        width: 980px; } }
