.badge {
  display: inline-block;
  text-align: center;
  background: #F0F4FD;
  color: #122351;
  font-size: 10px;
  line-height: 1;
  padding: 5px 6px;
  min-width: 20px;
  border-radius: 10px;
  font-weight: 500;
  text-transform: uppercase; }
  .badge.badge--orange {
    background: #F78B3D;
    color: #fff; }
  .badge.badge--yellow {
    color: #885945;
    background: #FDFAF0; }
  .badge.badge--red {
    color: #AC3434;
    background: #FDF0F0; }
  .badge.badge--small {
    padding: 3px;
    border-radius: 8px; }
  .badge.badge--large {
    font-size: 14px;
    line-height: 15px;
    border-radius: 20px;
    padding: 5px 10px;
    min-width: 25px; }
  .badge.badge--grey {
    background: #E8E8E8;
    color: #000; }
  .badge.badge--white {
    background: #fff;
    color: #F78B3D; }
  .badge.badge--white-blue {
    background: #fff;
    color: #312E76; }
