.form-feedback__inner {
  max-width: 760px;
  margin: 0 auto; }

.form-feedback__body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0 20px; }

.form-feedback__message {
  grid-column: span 3; }

.form-feedback__footer {
  display: flex;
  justify-content: flex-end; }

.form-feedback__submit {
  flex: 0 0 auto;
  min-width: 170px; }
