.counter {
  display: flex;
  align-self: stretch;
  justify-content: flex-end;
  border: 1px solid #E7E7E7;
  background: #fff;
  border-radius: 4px; }
  .counter__button, .counter__input {
    height: 38px;
    background: none;
    border-radius: 0; }
  .counter__button {
    flex: 0 0 35px;
    width: 35px;
    position: relative;
    color: #A8A8A8;
    border: none; }
    .counter__button:active, .counter__button:focus {
      color: #F78B3D; }
  .counter__input {
    flex: 1 1 auto;
    text-align: center;
    font-size: 12px;
    line-height: 18px;
    padding: 5px;
    min-width: 40px;
    font-weight: 700;
    border-width: 0 1px 0 1px;
    border-style: solid;
    border-color: #E7E7E7;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: textfield; }
    .counter__input::-webkit-outer-spin-button, .counter__input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0; }
