.contacts__map {
  margin-bottom: 40px; }

.contacts__item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start; }
  .contacts__item p {
    margin: 0; }
  .contacts__item__icon {
    flex: 0 0 auto;
    margin-right: 15px; }
  .contacts__item__title {
    font-weight: 500;
    margin-bottom: 5px; }
  .contacts__item__body {
    flex: 1 1 auto; }

.contacts__list {
  display: grid;
  grid-gap: 20px 40px;
  grid-template-columns: repeat(2, 1fr); }

.contacts__form {
  margin-top: 30px; }

@media (min-width: 1280px) {
  .contacts__body {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start; }
  .contacts__list {
    flex: 0 0 600px; }
  .contacts__form {
    flex: 1 1 auto;
    margin-top: 0; } }
