.form-sizes {
  padding: 15px;
  background: #fff; }
  .form-sizes__head {
    font-size: 14px;
    font-weight: 600;
    padding-bottom: 15px;
    border-bottom: 1px solid #F5F5F5; }
  .form-sizes__footer {
    border-top: 1px solid #F5F5F5;
    padding: 20px 15px 5px 15px;
    margin: 0 -15px; }
  .form-sizes__item-outer {
    border-bottom: 1px solid #F5F5F5; }
    .form-sizes__item-outer:last-child {
      border-bottom: none; }
  .form-sizes__item {
    margin: 0 -15px; }
