.colors-search__head {
  margin-bottom: 20px; }

.colors-search__pricing {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #F5F5F5; }

.colors-search__item {
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 1px solid #E7E7E7;
  scroll-snap-align: start; }
  .colors-search__item:last-child {
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom: none; }

.colors-search__item-head {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 20px; }

.colors-search__item-title {
  font-weight: 700; }

.colors-search__item-type {
  font-size: 12px;
  font-weight: 500; }

.colors-search__colors {
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, 60px); }

.colors-search__options {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap; }

.colors-search__option {
  margin: 0 10px 15px 0; }

@media (min-width: 1024px) {
  .colors-search__body {
    padding-right: 30px;
    max-height: 550px;
    overflow-y: auto;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    overflow-scrolling: touch;
    -webkit-overflow-scrolling: touch; }
    .colors-search__body::-webkit-scrollbar, .colors-search__body::-webkit-scrollbar-track, .colors-search__body::-webkit-scrollbar-thumb {
      border-radius: 3px; }
    .colors-search__body::-webkit-scrollbar:horizontal {
