.option-order {
  display: inline-flex;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .option-order [type="radio"],
  .option-order [type="checkbox"] {
    display: none; }
  .option-order__radio {
    display: block;
    color: #F78B3D;
    flex: 0 0 20px;
    width: 20px;
    height: 20px;
    position: relative; }
    .option-order__radio:before, .option-order__radio:after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      box-sizing: border-box;
      border-radius: 50%;
      transition: opacity 0.3s ease-out, transform 0.3s ease-out; }
    .option-order__radio::before {
      width: 14px;
      height: 14px;
      margin-left: -7px;
      margin-top: -7px;
      border: 1px solid currentColor; }
    .option-order__radio::after {
      background: currentColor;
      width: 6px;
      height: 6px;
      margin-left: -3px;
      margin-top: -3px;
      transform: scale(0);
      opacity: 0; }
    :checked ~ .option-order__radio::after {
      transform: scale(1);
      opacity: 1; }
  .option-order__body {
    flex: 1 1 auto;
    margin-left: 5px; }
  .option-order__title {
    font-size: 14px;
    line-height: 20px;
    font-weight: 600; }
  @media (min-width: 1024px) {
    .option-order__title {
      font-size: 1em; } }
