.form-search {
  display: block;
  position: relative; }
  .form-search__submit {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 40px;
    background: none;
    border: none;
    padding: 0;
    font-size: 20px;
    color: #A9A9A9; }
    :focus ~ .form-search__submit {
      color: #F78B3D; }
  .form-search__input[type="search"], .form-search__input[type="text"] {
    padding-left: 40px; }
  .form-search.form-search--round .form-search__input[type="search"],
  .form-search.form-search--round .form-search__input[type="text"] {
    border-radius: 20px; }
