.menu-main {
  color: #202020;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400; }
  .menu-main ul {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 0;
    margin: 0 -10px; }
    .menu-main ul li {
      display: inline-block;
      padding: 0 10px;
      margin: 0; }
  .menu-main__item {
    white-space: nowrap; }
    .menu-main__item.is-active, .menu-main__item:hover {
      color: #F78B3D; }
  .menu-main__item-link {
    color: currentColor;
    text-decoration: none; }
  .menu-main.menu-main--center ul {
    justify-content: center; }
