.breadcrumbs {
  color: #898989;
  font-weight: 500; }
  .breadcrumbs__back, .breadcrumbs__sep, .breadcrumbs__icon, .breadcrumbs__item {
    vertical-align: middle; }
  .breadcrumbs__back {
    display: inline-flex;
    align-items: center;
    color: #F78B3D;
    font-weight: 700; }
  .breadcrumbs__sep {
    display: inline-block;
    font-size: 0;
    margin: 0 10px; }
    .breadcrumbs__sep::after {
      content: '';
      display: inline-block;
      vertical-align: middle;
      background: currentColor;
      width: 1px;
      height: 9px; }
  .breadcrumbs__item {
    display: inline;
    text-decoration: none; }
    .breadcrumbs__item[href] {
      color: #000; }
  .breadcrumbs__icon {
    display: inline-block;
    font-size: 15px; }
  @media (min-width: 1024px) {
    .breadcrumbs {
      color: #BABABA;
      font-weight: 400; }
      .breadcrumbs__back {
        font-weight: 400; } }
