.product-card {
  display: flex;
  align-items: flex-start; }
  .product-card__main {
    flex: 1 1 auto;
    max-width: calc(100% - 350px);
    overflow: hidden; }
  .product-card__aside {
    flex: 0 0 350px;
    max-width: 350px;
    margin-left: 30px; }
  .product-card__gallery {
    margin-bottom: 30px; }
  .product-card__specs, .product-card__desc {
    margin-bottom: 40px; }
  .product-card__specs-title {
    margin: 0 0 15px 0; }
  .product-card__specs-table {
    display: table;
    width: 100%;
    border-collapse: separate;
    border-spacing: 10px 0;
    border: none; }
    .product-card__specs-table th,
    .product-card__specs-table td {
      text-align: left;
      padding: 15px 0;
      border-top: 1px solid #ECECEC;
      vertical-align: middle; }
    .product-card__specs-table th {
      padding-right: 10px;
      font-weight: 700; }
    .product-card__specs-table tr:last-child th,
    .product-card__specs-table tr:last-child td {
      border-bottom: 1px solid #ECECEC; }
