.icon-box {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border: none;
  padding: 0;
  background: #F4F4F4;
  color: #000;
  line-height: 0;
  font-size: 40px;
  width: 1em;
  height: 1em;
  border-radius: 12px;
  transition: color 0.3s ease-out, background 0.3s ease-out; }
  .icon-box__icon {
    font-size: 0.5em; }
  .icon-box.icon-box--green {
    background: #40AE52;
    color: #fff; }
  .icon-box.icon-box--orange {
    color: #fff;
    background: #F78B3D linear-gradient(to bottom, rgba(255, 255, 255, 0.2), #F78B3D); }
  .icon-box.icon-box--blue {
    background: #312e76;
    color: #fff; }
  .icon-box.icon-box--white {
    background: #fff;
    color: #312E76; }
  .icon-box.icon-box--round {
    border-radius: 0.5em; }
  .icon-box.icon-box--small {
    font-size: 32px; }
  .icon-box.icon-box--button {
    background: #fff;
    color: #F78B3D;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05); }
    .icon-box.icon-box--button:hover {
      background: #F78B3D;
      color: #fff; }
