.review {
  padding: 15px;
  color: #000;
  background: #FFFFFF;
  border-radius: 6px;
  box-shadow: 0 0 2px rgba(160, 160, 160, 0.4); }
  .review__head {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 5px; }
  .review__title {
    flex: 1 1 auto;
    color: #000;
    font-weight: 700; }
  .review__date {
    color: #9E9E9E;
    margin-left: 20px;
    font-weight: 400; }
  .review__body {
    margin-top: 5px; }
  @media (min-width: 1024px) {
    .review {
      padding: 20px; }
      .review__head {
        margin-bottom: 10px; } }
