@keyframes product-description {
  0% {
    opacity: 0;
    transform: translateY(-10px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

.product-description {
  border-top: 1px solid transparent; }
  .product-description p {
    margin: 0 0 2em 0; }
  .product-description__item {
    margin-bottom: 30px; }
  .product-description__body {
    display: none;
    animation: product-description 0.3s ease-out; }
  .product-description__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative; }
    .product-description__footer::before, .product-description__footer::after {
      content: '';
      display: block;
      height: 1px;
      background: #ECECEC;
      flex: 1 1 auto; }
  .product-description__toggle {
    position: relative;
    flex: 0 0 auto;
    min-width: 220px;
    margin: 0 20px; }
  .product-description.is-active {
    padding-top: 30px;
    border-color: #ECECEC; }
    .product-description.is-active .product-description__footer {
      margin-top: 30px; }
    .product-description.is-active .product-description__body {
      display: block; }
