.form-checkout__user, .form-checkout__payment, .form-checkout__delivery, .form-checkout__message {
  margin-bottom: 30px; }

@media (min-width: 1024px) {
  .form-checkout {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(2, 1fr); }
    .form-checkout__user, .form-checkout__payment, .form-checkout__delivery, .form-checkout__message {
      margin-bottom: 0; }
    .form-checkout__user, .form-checkout__delivery, .form-checkout__payment {
      grid-row: span 2; } }

@media (min-width: 1024px) {
  .form-checkout {
    grid-template-columns: repeat(3, 1fr); }
    .form-checkout__delivery {
      grid-row: span 3; }
    .form-checkout__message {
      grid-column: span 2;
      grid-row: span 2; } }
