@keyframes form-success {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.form-success {
  text-align: center;
  max-width: 220px;
  margin: 0 auto;
  animation: form-success 0.3s ease-out; }
  .form-success p {
    margin: 0; }
  .form-success__icon {
    margin: 20px 0;
    line-height: 0; }
  .form-success__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 5px; }
  .form-success__footer {
    text-align: center;
    margin-top: 20px; }
  @media (min-width: 1024px) {
    .form-success__button {
      display: inline-block;
      min-width: 140px; } }
