.option-button {
  display: inline-block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .option-button [type="radio"],
  .option-button [type="checkbox"] {
    display: none; }
  .option-button__btn {
    display: block;
    padding: 10px;
    font-size: 14px;
