@keyframes notify-item {
  0% {
    visibility: hidden;
    opacity: 0;
    transform: translateX(-10px); }
  100% {
    visibility: visible;
    opacity: 1;
    transform: translateX(0); } }

.notify {
  position: fixed;
  z-index: 300;
  left: 100px;
  bottom: 0;
  transition: opacity 0.3s ease-out, visibility 0.3s ease-out, transform 0.3s ease-out; }
  .notify__item {
    display: none;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 20px;
    padding: 15px;
    border-radius: 6px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: #fff;
    background: rgba(0, 0, 0, 0.75);
    animation: notify-item 0.3s ease-out; }
    .notify__item.is-active {
      display: flex; }
  .notify__item-icon {
    flex: 0 0 auto;
    margin-right: 20px; }
  .notify__item-body {
    flex: 1 1 auto; }
  .notify.is-hidden {
    visibility: hidden;
    opacity: 0;
    transform: translateY(10px); }
  @media (min-width: 1280px) {
    .notify {
      left: 320px; } }
