@keyframes header-cart {
  0%,
  20%,
  40% {
    transform: translateX(2px); }
  60%,
  80% {
    transform: translateX(5px); }
  10%,
  30%,
  50% {
    transform: translateX(-2px); }
  70%,
  90% {
    transform: translateX(-5px); }
  100% {
    transform: translateX(0); } }

@keyframes header-search {
  0% {
    opacity: 0;
    max-width: 0; }
  100% {
    max-width: 100%;
    opacity: 1; } }

.header {
  position: relative;
  min-height: 80px;
  z-index: 200;
  background: #FAFAFA; }
  .header__top {
    padding: 10px 30px;
    background: #F4F4F4; }
  .header__bottom {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 20px 30px; }
  .header__bottom-left {
    flex: 1 1 auto;
    margin-right: 20px;
    position: relative; }
  .header__logo {
    font-size: 0;
    line-height: 0;
    flex: 0 0 110px;
    max-width: 110px;
    margin-right: 20px; }
  .header__search-toggle {
    display: none; }
  .header__menu {
    display: none;
    flex: 1 1 auto;
    margin: 0 20px; }
  .header__callback {
    flex: 0 0 auto;
    min-width: 160px;
    margin-left: 20px; }
  .header__cart {
    flex: 0 0 auto;
    margin-left: 20px; }
    .header__cart.is-animated {
      animation: header-cart 1s ease-in-out; }
  .header__cart-label {
    display: none; }
  .header__search {
    flex: 1 1 auto; }
  .header__phone {
    flex: 0 0 auto; }
  .header.header--sticky {
    position: fixed;
    left: 90px;
    right: 0;
    top: 0;
    z-index: 350; }
  .supports-backdrop-filter .header {
    background: rgba(250, 250, 250, 0.8);
    -webkit-backdrop-filter: blur(15px);
    backdrop-filter: blur(15px); }
  @media (min-width: 1024px) {
    .header__cart-label {
      display: inline; } }
  @media (min-width: 1440px) {
    .header.header--sticky {
      left: 0; } }
  @media (min-width: 1680px) {
    .header__top {
      display: none; }
    .header__menu {
      display: block; }
    .header__bottom-left {
      display: flex;
      align-items: center;
      justify-content: flex-start; } }
