.product-panel {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  position: relative;
  background: #fff;
  box-shadow: 0 0 2px rgba(160, 160, 160, 0.4);
  border-radius: 6px;
  padding: 40px 10px 10px 10px;
  min-height: 260px; }
  .product-panel__head {
    margin-bottom: 5px; }
  .product-panel__body {
    flex: 1 1 auto; }
  .product-panel__footer {
    margin-top: 8px; }
  .product-panel__image {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    line-height: 0;
    height: 80px; }
    .product-panel__image img {
      max-height: 80px; }
  .product-panel__badges {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    position: absolute;
    left: 0;
    top: 0;
    padding: 10px 0 0 10px;
    max-width: 100%; }
  .product-panel__badge {
    flex: 0 0 auto;
    margin: 0 5px 5px 0; }
    .product-panel__badge:last-child {
      margin-right: 0; }
  .product-panel__title {
    color: #122351;
    font-weight: 600;
    text-decoration: none; }
  .product-panel__prices {
    margin-bottom: 5px; }
  .product-panel__price {
    color: #000;
    font-weight: 700;
    margin-right: 5px; }
  .product-panel__price-old {
    color: #A9A9A9;
    font-weight: 400;
    text-decoration: line-through; }
  .product-panel__reviews {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start; }
  .product-panel__rating {
    flex: 0 0 auto; }
  .product-panel__reviews-count {
    flex: 0 0 auto;
    margin-left: 5px; }
  .product-panel__thumbs {
    position: absolute;
    right: 100%;
    top: 0;
    height: 100%;
    width: 100px;
    padding: 20px 15px;
    background: #fff;
    box-shadow: 0 0 2px rgba(160, 160, 160, 0.4);
    border-radius: 6px 0 0 6px; }
  .product-panel__thumb {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    line-height: 0;
    height: 50px;
    position: relative;
    margin-bottom: 15px;
    cursor: pointer; }
    .product-panel__thumb:last-child {
      margin-bottom: 0; }
    .product-panel__thumb img,
    .product-panel__thumb [data-src] {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .product-panel__slide {
    display: none; }
    .product-panel__slide.is-active {
      display: block; }
  .product-panel__overlay {
    display: none;
    background: #fff;
    box-shadow: 0 0 2px rgba(160, 160, 160, 0.4);
    border-radius: 0 6px 6px 0;
    position: absolute;
    right: 0;
    top: 0;
    left: 0;
    bottom: -50px;
    z-index: -1; }
  .product-panel.product-panel--overlay {
    min-height: 290px; }
    .product-panel.product-panel--overlay .product-panel__footer {
      position: absolute;
      bottom: 15px;
      left: 15px;
      right: 15px;
      margin-top: 0; }
    .product-panel.product-panel--overlay:hover {
      box-shadow: none;
      border-radius: 0;
      z-index: 300; }
      .product-panel.product-panel--overlay:hover .product-panel__overlay {
        display: block; }
  @media (min-width: 1024px) {
    .product-panel {
      min-height: 350px;
      font-size: 15px;
      padding: 40px 15px 15px 15px; }
      .product-panel__title {
        font-weight: 500; }
      .product-panel__badges {
        padding: 15px 0 0 15px; }
      .product-panel__badge {
        margin: 0 10px 10px 0; }
      .product-panel__image {
        height: 120px; }
        .product-panel__image img {
          max-height: 120px; } }
