.pager {
  display: flex;
  align-items: center;
  justify-content: flex-start; }
  .pager ul {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0;
    margin: 0 -5px; }
    .pager ul li {
      display: inline-block;
      padding: 0 5px;
      margin: 0; }
  .pager__item {
    display: inline-block;
    text-align: center;
    font-size: 12px;
    line-height: 20px;
    padding: 1px;
    min-width: 22px;
    min-height: 22px;
    border-radius: 11px;
    font-weight: 700;
    text-decoration: none;
    color: #fff;
    background: #312E76; }
    .pager__item.is-active {
      color: #312E76;
      background: #F0F4FD; }
  .pager__title {
    margin-right: 15px; }
  .pager.pager--right {
    justify-content: flex-end; }
