.button {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #000;
  background: #fff;
  border: 1px solid #E0E8EB;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  padding: 9px 10px;
  border-radius: 4px;
  transition: background-color 0.3s ease-out; }
  .button:hover {
    background: #F0F4FD; }
  .button.is-active {
    color: #F78B3D; }
    .button.is-active .button__icon {
      color: currentColor; }
  .button__icon {
    color: #A8A8A8;
    font-size: 20px;
    margin-right: 5px;
    vertical-align: top; }
  .button__badge {
    display: inline-block;
    border-radius: 10px;
    line-height: 20px;
    padding: 0 5px;
    background: #A8A8A8;
    font-size: 0.85em;
    font-weight: 700;
    margin-left: 5px; }
  @media (min-width: 1024px) {
    .button {
      font-size: 14px; } }
  .button.button--black {
    color: #fff;
    background: #3A3A3A;
    border-color: #3A3A3A; }
    .button.button--black:hover {
      background: #202020;
      border-color: #202020; }
  .button.button--white {
    background: #fff;
    border-color: #fff;
    color: #000; }
  .button.button--grey {
    background: #E4E4E4;
    border-color: #E4E4E4;
    color: #000; }
    .button.button--grey:hover {
      background: #C7C7C7;
      border-color: #C7C7C7; }
  .button.button--orange {
    background: #F78B3D;
    border-color: #F78B3D;
    color: #fff; }
    .button.button--orange .button__badge {
      background: #fff;
      color: #F78B3D; }
    .button.button--orange .button__icon {
      color: currentColor; }
    .button.button--orange:hover {
      background: #F0741B;
      border-color: #F0741B; }
      .button.button--orange:hover .button__badge {
        color: #F0741B; }
  .button.button--blue {
    background: #F0F4FD;
    border-color: #F0F4FD;
    color: #312E76; }
    .button.button--blue .button__icon {
      color: currentColor; }
    .button.button--blue:hover {
      background: #DFE7F9;
      border-color: #DFE7F9; }
  .button.button--block {
    display: block;
    width: 100%; }
  .button.button--small {
    font-weight: 700;
    padding-top: 4px;
    padding-bottom: 4px; }
  .button.button--large {
    padding-top: 11px;
    padding-bottom: 11px; }
  .button.button--round {
    border-radius: 20px; }
    .button.button--round.button--large {
      border-radius: 22px; }
