.option {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 14px;
  line-height: 15px;
  font-weight: 400;
  padding: 15px; }
  .option [type="radio"],
  .option [type="checkbox"] {
    display: none; }
  .option__radio, .option__checkbox {
    display: block;
    position: relative;
    font-size: 16px;
    width: 1em;
    height: 1em;
    flex: 0 0 auto;
    margin-left: 10px;
    color: #F78B3D; }
  .option__checkbox {
    opacity: 0;
    transform: scale(0);
    transition: opacity 0.3s ease-out, transform 0.3s ease-out; }
    :checked ~ .option__checkbox {
      transform: scale(1);
      opacity: 1; }
  .option__radio {
    border-radius: 50%;
    border: 1px solid currentColor; }
    .option__radio::after {
      content: '';
      display: block;
      position: absolute;
      box-sizing: border-box;
      border-radius: 50%;
      background: currentColor;
      top: 50%;
      left: 50%;
      width: 6px;
      height: 6px;
      margin-left: -3px;
      margin-top: -3px;
      transform: scale(0);
      opacity: 0;
      transition: opacity 0.3s ease-out, transform 0.3s ease-out; }
    :checked ~ .option__radio::after {
      transform: scale(1);
      opacity: 1; }
  .option__title {
    flex: 1 1 auto; }
    :checked ~ .option__title {
      color: #F78B3D;
      font-weight: 500; }
  @media (min-width: 1024px) {
    .option:hover {
      background: #F78B3D;
      color: #fff; }
      .option:hover .option__title,
      .option:hover .option__radio,
      .option:hover .option__checkbox {
        color: currentColor; } }
