.cart__total {
  margin-bottom: 30px; }

.cart__item {
  margin-bottom: 30px; }

.cart__aside {
  margin-top: 30px;
  padding: 30px;
  border-radius: 6px;
  background: #F7F6FF; }

@media (min-width: 1280px) {
  .cart {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 1100px; }
    .cart__main {
      flex: 1 1 auto; }
    .cart__aside {
      flex: 0 0 350px;
      max-width: 350px;
      margin-left: 30px;
      margin-top: 0; } }
