.product-cart {
  display: block;
  position: relative;
  border-radius: 6px;
  box-shadow: 0 0 2px rgba(160, 160, 160, 0.4);
  padding: 15px; }
  .product-cart__head {
    margin-bottom: 15px; }
  .product-cart__footer {
    display: flex;
    align-items: flex-end;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin-top: 15px;
    padding-top: 15px;
    border-top: 1px solid #E7E7E7; }
  .product-cart__image {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 0;
    line-height: 0;
    height: 160px; }
    .product-cart__image img {
      max-height: 160px; }
  .product-cart__title {
    font-size: 14px;
    color: #122351;
    text-decoration: none;
    font-weight: 600; }
  .product-cart__prices {
    font-weight: 700;
    font-size: 15px;
    margin: 5px 0 10px 0;
    color: #000; }
  .product-cart__price {
    margin-right: 10px; }
  .product-cart__price-old {
    color: #A9A9A9;
    text-decoration: line-through; }
  .product-cart__specs {
    font-weight: 600; }
    .product-cart__specs dl {
      display: block;
      margin: 5px 0; }
    .product-cart__specs dd,
    .product-cart__specs dt {
      display: inline; }
  .product-cart__count, .product-cart__delete {
    flex: 0 0 auto; }
  @media (min-width: 1024px) {
    .product-cart {
      display: flex;
      align-items: stretch;
      padding: 30px; }
      .product-cart__title {
        font-size: 15px; }
      .product-cart__prices {
        font-size: 16px; }
      .product-cart__specs {
        font-weight: 400; }
      .product-cart__body {
        flex: 1 1 auto; }
      .product-cart__head {
        flex: 0 1 260px;
        margin: 0 30px 0 0; }
      .product-cart__footer {
        flex: 0 0 120px;
        max-width: 120px;
        flex-direction: column;
        align-items: stretch;
        border: none;
        margin: 0 0 0 30px;
        padding: 0; }
      .product-cart__count {
        margin-bottom: 20px; }
      .product-cart__delete {
        margin: auto 0 0 0; } }
