.product-gallery__main {
  border-radius: 10px;
  border: 1px solid #F4F4F4;
  padding: 15px;
  position: relative; }

.product-gallery__badges {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  position: absolute;
  left: 0;
  top: 0;
  padding: 15px;
  max-width: 100%;
  z-index: 10; }

.product-gallery__badge {
  flex: 0 0 auto;
  margin: 0 5px 5px 0; }

.product-gallery__image {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0;
  line-height: 0;
  height: 220px; }
  .product-gallery__image img {
    max-height: 220px; }

.product-gallery__thumbs {
  display: grid;
  grid-template-columns: repeat(auto-fill, 50px);
  grid-gap: 10px;
  margin-top: 15px; }

.product-gallery__thumb {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 5px;
  background: #fff;
  font-size: 0;
  outline: none;
  border: 1px solid #F4F4F4;
  cursor: pointer;
  padding: 50%;
  transition: box-shadow 0.3s ease-out; }
  .product-gallery__thumb img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain; }
  .product-gallery__thumb:hover, .product-gallery__thumb:focus, .product-gallery__thumb:active {
    border-color: #F78B3D; }
  .product-gallery__thumb.is-active {
    border-color: #F78B3D;
    box-shadow: 0 0 0 1px #F78B3D; }

@media (min-width: 1024px) {
  .product-gallery__main {
    padding: 30px; }
  .product-gallery__image {
    height: 400px; }
    .product-gallery__image img {
      max-height: 400px; }
  .product-gallery__thumbs {
    margin-top: 30px;
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fill, 96px); }
  .product-gallery__badges {
    padding: 20px; } }
