.color-item {
  display: block;
  text-align: center;
  cursor: pointer; }
  .color-item__image {
    display: block;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    font-size: 0;
    width: 60px;
    height: 60px;
    margin-bottom: 10px; }
    .color-item__image::after {
      content: '';
      display: none;
      position: absolute;
      border-radius: inherit;
      box-sizing: border-box;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      border: 2px solid #fff; }
    .color-item__image img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .color-item__title {
    font-size: 12px;
    font-weight: 500; }
  .color-item__icon {
    color: #fff;
    font-size: 30px;
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
    opacity: 0;
    transform: scale(0.5); }
  .color-item.is-active .color-item__image {
    border: 2px solid currentColor; }
    .color-item.is-active .color-item__image::after {
      display: block; }
  .color-item.is-checked .color-item__icon {
    opacity: 1;
    transform: scale(1); }
  .color-item.color-item--horizontal {
    display: flex;
    align-items: center;
    text-align: left;
    font-size: 14px; }
    .color-item.color-item--horizontal .color-item__title {
      flex: 1 1 auto; }
    .color-item.color-item--horizontal .color-item__image {
      flex: 0 0 30px;
      width: 30px;
      height: 30px;
      margin-bottom: 0;
      margin-right: 10px; }
  .color-item.color-item--orange .color-item__image {
    color: #FF6B00; }
