.nav-slider ul {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0 -5px; }
  .nav-slider ul li {
    display: inline-block;
    padding: 0 5px;
    margin: 0; }

.nav-slider__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #F78B3D;
  border: 1px solid #F78B3D;
  border-radius: 15px;
  color: #fff;
  font-size: 15px;
  position: relative;
  height: 30px;
  min-width: 30px;
  padding: 5px 10px; }
  .nav-slider__btn[disabled] {
    cursor: not-allowed;
    color: #D8D8D8; }

.nav-slider__btn-label {
  display: inline-block;
  font-size: 12px;
  line-height: 15px;
  font-weight: 500;
  padding: 0 5px; }
