.l-desktop {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  flex-direction: column;
  min-height: 100vh;
  min-width: 1024px;
  padding-left: 90px; }
  .l-desktop__head {
    margin-top: 20px;
    margin-bottom: 20px; }
  .l-desktop__content {
    padding-bottom: 50px; }
  .l-desktop__main {
    flex: 1 1 auto; }
  .l-desktop__footer {
    margin-top: auto; }
  @media (min-width: 1440px) {
    .l-desktop {
      padding-left: 270px; } }
